import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Select </h1>
                <blockquote className='page-tip'>
                    <br/>
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo1></Demo1>
                        </Label>
                        <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label>
                    </Col>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        <Label className='code-box'>
                            <Demo5></Demo5>
                        </Label>
                    </Col>

                    <Col grid={1} className='code-col'>
                        <Label className='code-box'>
                            <Demo4></Demo4>
                        </Label>
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Select</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'valueField', desc: 'value', type: 'string', default: 'id'},
                    {param: 'textField', desc: 'text', type: 'string', default: 'text'},
                    {param: 'sep', desc: '', type: 'string', default: ','},
                    {param: 'value', desc: '', type: 'string', default: ''},
                    {param: 'data', desc: 'select', type: 'array', default: ''},
                    {param: 'placeholder', desc: 'placeholder', type: 'string', default: ''},
                    {param: 'optionsTpl', desc: '', type: 'string', default: ''},
                    {param: 'name', desc: 'formname', type: 'string', default: ''},
                    {param: 'multi', desc: '', type: 'boolean', default: 'false'},
                    {param: 'onChange', desc: '', type: 'function', default: ''},
                    {param: 'hasEmptyOption', desc: 'Select ', type: 'boolean', default: 'false'},
                    {param: 'choiceText', desc: 'Select ', type: 'string', default: '----'},
                    {param: 'url', desc: '', type: 'string', default: ''}
                ]}></Table>


                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>getValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'String'} sep
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setValue(value)</i>
                        <span>Select</span>
                        <ul>
                            <li>
                                value {'String'} sep
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setData(data)</i>
                        <span>Select</span>
                        <ul>
                            <li>
                                data {'Array'} Select
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>disable()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>enable()</i>
                        <span></span>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



